import React from 'react';
import { default as MuiClickAwayListener, ClickAwayListenerProps } from '@mui/material/ClickAwayListener';

const ClickAwayListener = React.forwardRef<HTMLElement, ClickAwayListenerProps>( ( props, ref ) => {
	return (
		<MuiClickAwayListener
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ClickAwayListenerProps>;

export default ClickAwayListener;
